<template>
  <div class="page-form">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'pageForm',
  components: {},
  data () {
    return {
    }
  },
  methods: {
    getFormAllResult () {
      let result = { isOk: true, value: {} }
      this.$children.forEach(item => {
        let curRes = item.transferResult()
        if (!curRes.isOk) {
          result.isOk = false
        }
        result.value[curRes.code] = curRes
      })
      return result
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .page-form{
    font-family: $font-family-Medium!important;
    width: 100%;
  }
</style>



