<template>
  <div class="form-item-input" :class="{'form-item-input-false': !this.itemData.isOk}">
    <div class="item-label">{{itemData.label}}:</div>
    <div class="item-content">
      <input type="text" @blur="handleBlur" :placeholder="itemData.placeholder" v-model="itemData.value"/>
    </div>
    <div class="item-tip"></div>
  </div>
</template>
<script>
export default {
  name: 'formItemInput',
  components: {},
  props: {
    itemData: {
      type: Object,
      required: true,
      default: () => {
        return { code: '', value: '', label: '', placeholder: '', isOk: true, validateMess: '', regex: /^[\s\S]*.*[^\s][\s\S]*$/ }
      }
    }
  },
  data () {
    return {
    }
  },
  created () {
  },
  methods: {
    validValue () {
      this.itemData.isOk = this.itemData.regex.test(this.itemData.value)
    },
    handleBlur () {
      this.validValue()
    },
    transferResult () {
      this.validValue()
      return Object.assign({}, this.itemData)
    }
  },
  watch: {
    'itemData.value': {
      handler (val) {
        this.validValue()
      }
    }
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .form-item-input{
    font-family: $font-family-Medium!important;
    display: flex;align-items: center;justify-content: center;flex-direction: row;
    width: 100%;height: 84px;
    border-bottom: 1.2px solid $whitee6e6e6;
    font-size: 28px;
    &-false{
      border-bottom: 1px solid #FF9999!important;
      .item-label{
        color: #FF9999!important;
      }
      .item-content{
        input{
          &::placeholder{
            color: #fe9a9d87!important;
          }
        }
      }
    }
    .item-label{
      width: 160px;
      color: $black333333;
      font-weight: 600;
    }
    .item-content{
      flex: 1;
      color: $black333333;
      font-family: $font-family-Medium!important;
      input{
        width: 100%;
        &::placeholder{
          color: $black999999;
          font-family: $font-family-Medium!important;
        }
      }
    }
    .item-tip{
      width: 20px;
    }
  }
</style>



