<template>
  <div class="form-item-address" :class="{'form-item-address-false': !this.itemData.isOk}"  @click.stop="showAddress = true">
    <div class="item-label">{{itemData.label}}:</div>
    <div class="item-content">
      <input type="text" readonly :placeholder="itemData.placeholder" v-model="inputStr"/>
    </div>
    <div class="item-tip">
      <van-icon name="arrow" color="#999999"/>
    </div>
    <popup-address :show.sync="showAddress" :selectedCode.sync="itemData.value" @result="getAddress"></popup-address>
  </div>
</template>
<script>
import PopupAddress from '@/components/Popup/address'
export default {
  name: 'formItemAddress',
  components: { PopupAddress },
  props: {
    itemData: {
      type: Object,
      required: true,
      default: () => {
        return { code: '', value: '', label: '', placeholder: '', isOk: true, validateMess: '', regex: /^[\s\S]*.*[^\s][\s\S]*$/ }
      }
    }
  },
  data () {
    return {
      showAddress: false,
      inputStr: ''
    }
  },
  created () {
  },
  methods: {
    getAddress (result) {
      let tempstr = result.reduce((res, item) => {
        return `${res}-${item.name}`
      }, '')
      this.inputStr = tempstr.substring(1)
    },
    validValue () {
      this.itemData.isOk = this.itemData.regex.test(this.itemData.value)
    },
    transferResult () {
      this.validValue()
      return Object.assign({}, this.itemData)
    }
  },
  watch: {
    showAddress: {
      handler (val) {
        if (!val) {
          this.validValue()
        }
      }
    }
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .form-item-address{
    font-family: $font-family-Medium!important;
    display: flex;align-items: center;justify-content: center;flex-direction: row;
    width: 100%;height: 84px;
    border-bottom: 1.2px solid $whitee6e6e6;
    font-size: 28px;
    &-false{
      border-bottom: 1px solid #FF9999!important;
      .item-label{
        color: #FF9999!important;
      }
      .item-content{
        input{
          &::placeholder{
            color: #fe9a9d87!important;
          }
        }
      }
      .item-tip{
        i{
          color: #FF9999!important;
        }
      }
    }
    .item-label{
      width: 160px;
      color: $black333333;
      font-weight: 600;
    }
    .item-content{
      flex: 1;
      color: $black333333;
      font-family: $font-family-Medium!important;
      input{
        width: 100%;
        &::placeholder{
          color: $black999999;
          font-family: $font-family-Medium!important;
        }
      }
    }
    .item-tip{
      width: 20px;
    }
  }
</style>



