<template>
  <div class='popup-address'>
    <van-popup v-model="isShow" position="bottom">
      <van-area :area-list="areaList" :value="selectedCodeTemp" @cancel="cancel" @confirm="confirm" @change="change"/>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'popupAddress',
  components: {},
  inheritAttrs: false,
  props: {
    show: { type: Boolean, default: false, required: true },
    selectedCode: { type: String, default: '', required: true }
  },
  data () {
    return {
      isShow: false,
      selectedCodeTemp: '',
      areaList: {
        province_list: {
          110000: '北京市',
          120000: '天津市'
        },
        city_list: {
          110100: '北京市',
          120100: '天津市'
        },
        county_list: {
          110101: '东城区',
          110102: '西城区',
          110105: '朝阳区',
          110106: '丰台区',
          120101: '和平区',
          120102: '河东区',
          120103: '河西区',
          120104: '南开区',
          120105: '河北区'
        }
      }
    }
  },
  computed: {},
  created () {
  },
  methods: {
    change (e) {
    },
    confirm (selData) {
      this.$emit('update:selectedCode', selData[selData.length - 1].code)
      this.$emit('result', selData)
      this.isShow = false
    },
    cancel () {
      this.isShow = false
    }
  },
  watch: {
    show (val) {
      this.isShow = val
    },
    isShow (val) {
      if (!val) {
        this.$emit('update:show', false)
      }
    },
    selectedCode: {
      handler (val) {
        this.selectedCodeTemp = val
      },
      immediate: true
    }
  }
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .popup-address {
    font-family: $font-family-default!important;
  }
</style>



