<template>
  <div class="app-page address-add">
      <page-content class="page-content">
        <form-cus ref="formCus">
          <form-item-input :itemData = formData.name></form-item-input>
          <form-item-input :itemData = formData.phone></form-item-input>
          <form-item-address :itemData = formData.address></form-item-address>
          <form-item-input :itemData = formData.addressDetail></form-item-input>
        </form-cus>
        <div class="set-default vertical-center" @click="isDefault = ! isDefault">
          <img :src="isDefault ? 'static/images/commonicon/xz1.png' : 'static/images/commonicon/wxz1.png'"/>
          <p>设置默认地址</p>
        </div>
        <div class="submit-div">
          <button @click="clickSave">新增地址</button>
        </div>
      </page-content>
  </div>
</template>
<script>
import PageContent from '@/components/page/content'
import FormCus from '@/components/page/form/form'
import FormItemInput from '@/components/page/form/form-item-input'
import FormItemAddress from '@/components/page/form/form-item-address'
export default {
  name: 'addressAdd',
  components: { PageContent, FormCus, FormItemInput, FormItemAddress },
  data () {
    return {
      formData: {
        name: { code: 'name', value: '', label: '联系人', placeholder: '请输入收货人姓名', isOk: true, validateMess: '', regex: /^[\u4E00-\u9FA5A-Za-z\\s]+(·[\u4E00-\u9FA5A-Za-z]+)*$/ },
        phone: { code: 'phone', value: '', label: '手机号', placeholder: '请输入收货人手机号', isOk: true, validateMess: '', regex: /^1(3|4|5|7|8)\d{9}$/ },
        address: { code: 'address', value: '', label: '联系地址', placeholder: '请选择地址', isOk: true, validateMess: '', regex: /^[\s\S]*.*[^\s][\s\S]*$/ },
        addressDetail: { code: 'addressDetail', value: '', label: '详细地址', placeholder: '如街道、门牌号、楼栋号、单元号', isOk: true, validateMess: '', regex: /^[\s\S]*.*[^\s][\s\S]*$/ }
      },
      isDefault: false
    }
  },
  methods: {
    clickSave () {
      console.log(this.$refs.formCus.getFormAllResult())
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .address-add{
    font-family: $font-family-Medium!important;
    .page-content{
      padding: 10px 30px;
      position: relative;
      .form{
        width: 100%;
        .form-item{}
      }
      .set-default{
        width: 100%;
        padding-left: 20px;
        margin-top: 20px;
        img{width: 37px; height: 37px}
        p{
          margin-left: 20px;
          font-size: 28px;color: $black333333;
          font-weight: 600;
        }
      }
      .submit-div{
        height: 76px;
        position: absolute;
        bottom: 60px;
        left: 20px;right: 20px;
        button{
          width: 100%;
          height: 100%;
          border-radius: 10px;
          color: $whiteffffff;
          background-color: $green22ac38;
          font-size: 28px;
          font-family: $font-family-Bold;
          font-weight: 600;
          letter-spacing: 1px;
        }
      }
    }
  }
</style>



